import React, { useState } from 'react';
import { Button, FormControl, FormLabel, Select, MenuItem, Typography, TextField } from '@material-ui/core';

const FamilyTreeForm = ({ onFormSubmit }) => {
  const [numSponsors] = useState(10);
  const [filledFields, setFilledFields] = useState(0);

  const [formData, setFormData] = useState({
    numSponsors: 10,
    sponsors: [
      { relation: 'Father', name: '', id: 'root' },
      { relation: 'Mother', name: '', id: 'sponsor-1' },
      { relation: 'GrandFather-paternal', name: '', id: 'sponsor-2' },
      { relation: 'GrandMother-paternal', name: '', id: 'sponsor-3' },
      { relation: 'GrandFather-maternal', name: '', id: 'sponsor-4' },
      { relation: 'GrandMother-maternal', name: '', id: 'sponsor-5' },
      { relation: 'Uncle-paternal', name: '', id: 'sponsor-6' },
      { relation: 'Aunty-paternal', name: '', id: 'sponsor-7' },
      { relation: 'Uncle-maternal', name: '', id: 'sponsor-8' },
      { relation: 'Aunty-maternal', name: '', id: 'sponsor-9' },
    ],
    studentName: '',
    studentGender: '',
  });

  const handleChangeName = (index, event) => {
    const updatedSponsors = [...formData.sponsors];
    updatedSponsors[index] = { ...updatedSponsors[index], name: event.target.value };
    setFormData({ ...formData, sponsors: updatedSponsors });
    checkFilledFields();
  };

  const handleChangeStudentName = (event) => {
    setFormData({ ...formData, studentName: event.target.value });
    checkFilledFields();
  };

  const handleChangeStudentGender = (event) => {
    setFormData({ ...formData, studentGender: event.target.value });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const treeData = generateTreeData(formData);
    onFormSubmit(treeData);
    console.log(treeData);
  };

  const checkFilledFields = () => {
    let filledCount = 0;
    formData.sponsors.forEach((sponsor) => {
      if (sponsor.name !== '') {
        filledCount++;
      }
    });

    setFilledFields(filledCount);
  };

  const generateTreeData = (formData) => {
    const { sponsors, studentName, studentGender } = formData;
    const treeData = {
      name: studentName,
      gender: studentGender,
      children: [],
    };

    sponsors.forEach((sponsor, index) => {
      const { relation, name } = sponsor;
      const sponsorNode = {
        name: `${name}`,
        children: [],
      };

      treeData.children.push(sponsorNode);

      if (index === 0) {
        sponsorNode.id = 'root';
      } else {
        sponsorNode.id = `sponsor-${index}`;
        const parentIndex = Math.floor((index - 1) / 2);
        const parentNode = treeData.children[parentIndex];
        parentNode.children.push(sponsorNode);
      }
    });

    return treeData;
  };

  return (
    <form onSubmit={handleSubmit}>
      <TextField
        label="Student Name"
        value={formData.studentName}
        onChange={handleChangeStudentName}
        variant="outlined"
      />

     <FormControl style={{marginLeft: '15px'}}>
        <FormLabel>Student Gender</FormLabel>
        <Select value={formData.studentGender} onChange={handleChangeStudentGender}>
          <MenuItem value="male">Male</MenuItem>
          <MenuItem value="female">Female</MenuItem>
        </Select>
      </FormControl>
      <br />
      <br />

      {Array(numSponsors)
        .fill()
        .map((_, index) => (
          <div key={index}>
            <FormControl>
              <FormLabel>Relation for Sponsor {index + 1}</FormLabel>
              <Select value={formData.sponsors[index].relation}>
                <MenuItem value="Father">Father</MenuItem>
                <MenuItem value="Mother">Mother</MenuItem>
                <MenuItem value="GrandFather-paternal">Grandfather-pat</MenuItem>
                <MenuItem value="GrandMother-paternal">Grandmother-pat</MenuItem>
                <MenuItem value="GrandFather-maternal">Grandfather-mat</MenuItem>
                <MenuItem value="GrandMother-maternal">Grandmother-mat</MenuItem>
                <MenuItem value="Uncle-paternal">Uncle-pat</MenuItem>
                <MenuItem value="Aunty-paternal">Aunty-pat</MenuItem>
                <MenuItem value="Uncle-maternal">Uncle-mat</MenuItem>
                <MenuItem value="Aunty-maternal">Aunty-mat</MenuItem>
              </Select>
            </FormControl>
            <TextField
              label="Name"
              style={{ marginLeft: '15px' }}
              value={formData.sponsors[index].name}
              onChange={(event) => handleChangeName(index, event)}
              variant="outlined"
            />
          </div>
        ))}
      <br />
      {filledFields > 4 && (
        <Typography variant="caption" color="error">
          Fill only 4 fields
        </Typography>
      )}
      <br />

      <Button variant="contained" color="primary" type="submit">
        Submit
      </Button>
      <br />
    </form>
  );
};

export default FamilyTreeForm;
